.method-item {
  position: relative;
  min-height: 266px;
  background-color: @color-footer;
  margin-bottom: 20px;
}
.method-item__title {

  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  margin: 0;
  padding: 0;
  padding-top: 12px;
  padding-left: 20px;
  text-transform: uppercase;
  text-align: center;
  color: @color-text;


}

.method-item--losing .method-item__title {
  padding-top: 29px;
  padding-left: 11px;
}
.method-item--losing::before {
  content: "";
  position: absolute;

  width: 50px;
  height: 50px;
  background-color: @color-green-button;
  left: 23px;
  top: 24px;
  border-radius: 50%;
}
.method-item--losing::after {
  content: "";
  position: absolute;

  background-image: url("../img/icon-cat-slim.svg");
  background-position: 0 0;
  background-repeat: no-repeat;
  width: 35px;
  height: 49px;
  left: 30px;
  top: 21px;
  z-index: 2;


}
.method-item--gain::before {
  content: "";
  position: absolute;

  width: 50px;
  height: 50px;
  background-color: @color-green-button;
  left: 22px;
  top: 23px;
  border-radius: 50%;
}
.method-item--gain::after {
  content: "";
  position: absolute;

  background-image: url("../img/icon-cat-pro.svg");
  background-position: 0 0;
  background-repeat: no-repeat;
  width: 67px;
  height: 28px;
  left: 14px;
  top: 35px;
  z-index: 2;


}
.method-item--losing .method-item__description {
  padding-top: 6px;
  margin-left: 20px;
}
.method-item__description {
  width: 235px;
  font-size: 14px;
  line-height: 18px;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  width: 235px;
  margin: 0;
  padding: 0;
  padding-bottom: 23px;
  // padding-right: 25px;
  // padding-left: 20px;
  margin-top: 23px;
  color: @color-description-text;
  border-bottom: 2px solid #d9d9d9;
}
.method-item--losing .method-item__link {
  margin-top: 16px;
  &::before {
    content: "";

    position: absolute;
    background-image: url("../img/icon-right-arrow-long.svg");
    background-position: 0 0;
    background-repeat: no-repeat;
    width: 24px;
    height: 11px;
    top: 3px;
    left: 117px;

  }
  &:hover::before {
    background-image: url("../img/icon-right-arrow-verylong.svg");
    width: 33px;
    height: 11px;
  }
  &:focus::before {
    background-image: url("../img/icon-right-arrow-verylong.svg");
    width: 33px;
    height: 11px;
  }
  &:active::before {
    background-image: url("../img/icon-right-arrow-verylong.svg");
    width: 33px;
    height: 11px;
    color: #b7b7b7;
  }
}
.method-item--gain .method-item__title {
  padding-top: 29px;
  padding-left: 39px;
}
.method-item--gain .method-item__description {
  padding-top: 7px;
  margin-left: 20px;
}
.method-item--gain .method-item__link {
  margin-top: 17px;
  &::before {
    content: "";

    position: absolute;
    background-image: url("../img/icon-right-arrow-long.svg");
    background-position: 0 0;
    background-repeat: no-repeat;
    width: 24px;
    height: 11px;
    top: 3px;
    left: 115px;
  }
  &:hover::before {
    background-image: url("../img/icon-right-arrow-verylong.svg");
    width: 33px;
    height: 11px;
  }
  &:focus::before {
    background-image: url("../img/icon-right-arrow-verylong.svg");
    width: 33px;
    height: 11px;
  }
  &:active::before {
    background-image: url("../img/icon-right-arrow-verylong.svg");
    width: 33px;
    height: 11px;
    color: #b7b7b7;
  }
}
.method-item__link {
  position: relative;
  display: block;

  color: @color-text;
  font-size: 16px;
  line-height: 16px;
  text-decoration: none;
  text-transform: uppercase;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;

  margin-top: 20px;
  padding-bottom:20px;
  padding-left: 20px;


}
